html {
    box-sizing: border-box;
}
*,
*::after,
*::before {
    box-sizing: inherit;
}
body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
h1,h2,h3,h4,h5,h6 {
    margin: 0;
}
a {

    color: inherit;
    text-decoration: none;


}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}